





































import { Component, Vue } from "vue-property-decorator";
import BarAndNumber from "@/components/BarAndNumber.vue";
import CharButton from "@/components/CharButton.vue";

@Component({
  components: {
    BarAndNumber,
    CharButton
  }
})
export default class HoTwiPiTanme extends Vue {
  public answer = "";
  public isStart = false;
  public lastTime = 0;
  public point = 0;
  public hint = {
    screenName: "",
    tweet: ""
  };
  private baseApiUrl = "http://127.0.0.1/";
  private answers = [
    "とうきょう",
    "いけぶくろ",
    "しんじゅく",
    "しながわ",
    "うえの"
  ];
  rows = [
    [
      { char: "と" },
      { char: "う" },
      { char: "き" },
      { char: "ょ" },
      { char: "う" },
      { char: "　" }
    ],
    [
      { char: "い" },
      { char: "け" },
      { char: "ぶ" },
      { char: "く" },
      { char: "ろ" },
      { char: "　" }
    ],
    [
      { char: "し" },
      { char: "ん" },
      { char: "じ" },
      { char: "ゅ" },
      { char: "く" },
      { char: "　" }
    ],
    [
      { char: "　" },
      { char: "し" },
      { char: "な" },
      { char: "が" },
      { char: "わ" },
      { char: "　" }
    ],
    [
      { char: "　" },
      { char: "う" },
      { char: "　" },
      { char: "え" },
      { char: "　" },
      { char: "の" }
    ],
    [
      { char: "　" },
      { char: "　" },
      { char: "　" },
      { char: "　" },
      { char: "　" },
      { char: "　" }
    ]
  ];

  public created() {
    this.lastTime = 60;
  }

  public deleteChar(value: string, callback: Function) {
    var char = this.answer.charAt(this.answer.length - 1);
    if (char != value) {
      callback(false);
      return;
    }
    this.answer = this.answer.slice(0, this.answer.length - 1);
    console.log("delete:" + value);
    callback(true);
  }

  public addChar(value: string, callback: Function) {
    if (!this.isStart) {
      this.isStart = true;
      this.countDownTimer();
      console.log("start");
    }
    this.answer += value;
    console.log("add   :" + value);
    callback(true);
  }

  private countDownTimer() {
    this.lastTime--;
    if (this.lastTime != 0) {
      setTimeout(() => {
        this.countDownTimer();
      }, 1000);
    }
  }

  public onClickAnswerButton() {
    this.answers.forEach((item, index) => {
      if (item == this.answer) {
        this.point += this.lastTime * item.length;
        this.answer = "";
        console.log("正解");
        this.answers.splice(index, 1);
      }
    });

    if (this.answers.length == 0) {
      console.log("終了！：" + this.point);
    }
  }

  public onClickShowHint() {
    this.hint.tweet = "ひんとひんと";
    this.hint.screenName = "@hogehoge";
  }
}
